import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent {
  title = '這是個變數';
  myUrl = 'https://google.com.tw';
  inputData='';
  text="我是好長的一句話阿"
  constructor() {}
  changeTitle($event:MouseEvent){
    console.log($event)
    if($event.altKey){
      this.title = "我被改變了"
    } else{
      this.title = "你失敗了"
    }
   
  }
}
 